import axios from 'axios';

const API_KEY = '193148fb3e296bb7bc40d2f930865e2a';
const baseUrl = 'https://api.themoviedb.org/3/';

//         
export async function fetchDefaultMovies(pageNumber) {
  try {
    return await axios.get(
      `${baseUrl}trending/movie/day?api_key=${API_KEY}&language=en-US&include_image_language=en&page=${pageNumber}`
    );
  } catch (error) {
    console.error(error);
  }
}

//    ID
export async function fetchMovieTrailer(movieId) {
  try {
    return await axios.get(
      `${baseUrl}movie/${movieId}/videos?api_key=${API_KEY}&language=en-US`
    );
    // console.log(result)
  } catch (error) {
    console.error(error);

    // if (error.response.status === 404) {
    //     console.log(error.data.results.length)
    //     alert("Oops, there is not trailer")
    // }
  }
}
